import { Unit, CLASS_KEY } from "./Unit";
import { BHP_PER_KW } from "../constants";

export enum PowerUnit {
	W = "watt",
	kW = "kilowatt",
	BHP = "brake horsepower",
	SI = "watt",
}

export const POWER_CLASS_ID = Symbol.for("powerUnit").toString();

export class Power implements Unit {
	private _watt = 0;
	private _kilowatt = 0;
	private _bhp = 0;

	constructor(value: number, units = PowerUnit.W) {
		this[units] = value;
	}

	toJSON() {
		return {
			[CLASS_KEY]: POWER_CLASS_ID,
			[PowerUnit.SI]: this.SI
		}
	}

	get SI () {
		return this._watt;
	}

	set [PowerUnit.W] (value: number) {
		this._watt = value;
		this._kilowatt = value / 1000;
		this._bhp = this._kilowatt * BHP_PER_KW;
	}
	get [PowerUnit.W] () { return this._watt }

	set [PowerUnit.kW] (value: number) {
		this._watt = value * 1000;
		this._kilowatt = value;
		this._bhp = value * BHP_PER_KW;
	}
	get [PowerUnit.kW] () { return this._kilowatt }

	set [PowerUnit.BHP] (value: number) {
		this._bhp = value;
		this._kilowatt = value / BHP_PER_KW;
		this._watt = this._kilowatt * 1000;
	}
	get [PowerUnit.BHP] () { return this._bhp }
}
