import { Module, Store } from "vuex";
import { MapId, XAxisUnit, YAxisUnit } from "@/types";

export const NAMESPACE = "map"
const namespaced = (property: string): string => `${NAMESPACE}/${property}`;

export type MapConfig = {
	imageData: string;
	imageWidth: number;
	imageHeight: number;

	mapWidth: number;
	mapHeight: number;
	gridWidth: number;
	gridHeight: number;
	offsetX: number;
	offsetY: number;

	minX: number;
	maxX: number;
	xStep: number;
	xUnits: XAxisUnit;

	minY: number;
	maxY: number;
	yStep: number;
	yUnits: YAxisUnit;

	layers: {
		map: boolean;
		grid: boolean;
		points: boolean;
		curveFromOtherside: boolean;
	};
}

export type MapConfigState = Record<MapId, MapConfig>;

export const buildMapAccessor = <T extends keyof MapConfig>(
	map: MapId,
	property: T,
	store: Store<{}>
) => {
	return ({
		get: (): MapConfig[T] => store.getters[namespaced(property)](map),
		set: (input: MapConfig[T]) =>{ store.commit(namespaced(property), { map, [property]: input }) }
	})
}

const getGridHorizontalPositionAfterMapResize = (state: MapConfig, newWidth: number): [number, number] => {
	const offsetRatio = state.offsetX / state.mapWidth
	const widthRatio = state.gridWidth / state.mapWidth

	return [offsetRatio * newWidth, widthRatio * newWidth];
}

const getGridVerticalPositionAfterMapResize = (state: MapConfig, newHeight: number): [number, number] => {
	const offsetRatio = state.offsetY / state.mapHeight
	const heightRatio = state.gridHeight / state.mapHeight

	return [offsetRatio * newHeight, heightRatio * newHeight];
}

export const map: Module<MapConfigState, {}> = {
	namespaced: true,
	state: {
		coldSideMap: {
			imageData: "",
			imageWidth: 800,
			imageHeight: 600,

			mapWidth: 800,
			mapHeight: 600,
			gridWidth: 700,
			gridHeight: 500,
			offsetX: 50,
			offsetY: 550,

			minX: 0,
			maxX: 60,
			xStep: 5,
			xUnits: "corrected lbs/min",

			minY: 1,
			maxY: 4,
			yStep: 0.25,
			yUnits: "pressureRatio",

			layers: {
				map: true,
				grid: true,
				curveFromOtherside: true,
				points: true,
			}
		},
		hotSideMap: {
			imageData: "",
			imageWidth: 800,
			imageHeight: 600,

			mapWidth: 800,
			mapHeight: 600,
			gridWidth: 700,
			gridHeight: 500,
			offsetX: 50,
			offsetY: 550,

			minX: 1,
			maxX: 4,
			xStep: 0.25,
			xUnits: "pressureRatio",

			minY: 0,
			maxY: 25,
			yStep: 2.5,
			yUnits: "corrected lbs/min",

			layers: {
				map: true,
				grid: true,
				curveFromOtherside: false,
				points: true,
			}
		}
	},
	getters: {
		imageData: (state: MapConfigState) => (map: MapId) => state[map].imageData,
		imageWidth: (state: MapConfigState) => (map: MapId) => state[map].imageWidth,
		imageHeight: (state: MapConfigState) => (map: MapId) => state[map].imageHeight,
		mapWidth: (state: MapConfigState) => (map: MapId) => state[map].mapWidth,
		mapHeight: (state: MapConfigState) => (map: MapId) => state[map].mapHeight,
		gridWidth: (state: MapConfigState) => (map: MapId) => state[map].gridWidth,
		gridHeight: (state: MapConfigState) => (map: MapId) => state[map].gridHeight,
		offsetX: (state: MapConfigState) => (map: MapId) => state[map].offsetX,
		offsetY: (state: MapConfigState) => (map: MapId) => state[map].offsetY,
		minX: (state: MapConfigState) => (map: MapId) => state[map].minX,
		maxX: (state: MapConfigState) => (map: MapId) => state[map].maxX,
		xStep: (state: MapConfigState) => (map: MapId) => state[map].xStep,
		xUnits: (state: MapConfigState) => (map: MapId) => state[map].xUnits,
		minY: (state: MapConfigState) => (map: MapId) => state[map].minY,
		maxY: (state: MapConfigState) => (map: MapId) => state[map].maxY,
		yStep: (state: MapConfigState) => (map: MapId) => state[map].yStep,
		yUnits: (state: MapConfigState) => (map: MapId) => state[map].yUnits,
		layers: (state: MapConfigState) => (map: MapId) => state[map].layers,
	},
	mutations: {
		imageData: (state: MapConfigState, { map, imageData }: { map: MapId; imageData: string }) => { state[map].imageData = imageData },
		imageWidth: (state: MapConfigState, { map, imageWidth }: { map: MapId; imageWidth: number }) => { state[map].imageWidth = imageWidth },
		imageHeight: (state: MapConfigState, { map, imageHeight }: { map: MapId; imageHeight: number }) => { state[map].imageHeight = imageHeight },
		mapWidth: (state: MapConfigState, { map, mapWidth }: { map: MapId; mapWidth: number }) => {
			const [ offsetX, gridWidth ] = getGridHorizontalPositionAfterMapResize(state[map], mapWidth);
			state[map].mapWidth = mapWidth
			state[map].offsetX = offsetX
			state[map].gridWidth = gridWidth
		},
		mapHeight: (state: MapConfigState, { map, mapHeight }: { map: MapId; mapHeight: number }) => {
			const [ offsetY, gridHeight ] = getGridVerticalPositionAfterMapResize(state[map], mapHeight)
			state[map].mapHeight = mapHeight
			state[map].offsetY = offsetY
			state[map].gridHeight = gridHeight
		},
		gridWidth: (state: MapConfigState, { map, gridWidth }: { map: MapId; gridWidth: number }) => { state[map].gridWidth = gridWidth },
		gridHeight: (state: MapConfigState, { map, gridHeight }: { map: MapId; gridHeight: number }) => { state[map].gridHeight = gridHeight },
		offsetX: (state: MapConfigState, { map, offsetX }: { map: MapId; offsetX: number }) => { state[map].offsetX = offsetX },
		offsetY: (state: MapConfigState, { map, offsetY }: { map: MapId; offsetY: number }) => { state[map].offsetY = offsetY },
		minX: (state: MapConfigState, { map, minX }: { map: MapId; minX: number }) => { state[map].minX = minX },
		maxX: (state: MapConfigState, { map, maxX }: { map: MapId; maxX: number }) => { state[map].maxX = maxX },
		xStep: (state: MapConfigState, { map, xStep }: { map: MapId; xStep: number }) => { state[map].xStep = xStep },
		xUnits: (state: MapConfigState, { map, xUnits }: { map: MapId; xUnits: XAxisUnit }) => { state[map].xUnits = xUnits },
		minY: (state: MapConfigState, { map, minY }: { map: MapId; minY: number }) => { state[map].minY = minY },
		maxY: (state: MapConfigState, { map, maxY }: { map: MapId; maxY: number }) => { state[map].maxY = maxY },
		yStep: (state: MapConfigState, { map, yStep }: { map: MapId; yStep: number }) => { state[map].yStep = yStep },
		yUnits: (state: MapConfigState, { map, yUnits }: { map: MapId; yUnits: YAxisUnit }) => { state[map].yUnits = yUnits },
	}
}

