
import { defineComponent, computed, onMounted, ref, onBeforeUnmount } from "vue"
import { useStore } from "vuex";
import { useGridWatchers, buildGrid } from "./grid-watchers";
import { MapId } from "@/types";
import { useMouseControl } from "./mouse-control";
import Plane from "./Plane.vue";
import { buildMapAccessor } from "@/store/map";

type GridProps = {
	canvasId: string;
	mapId: MapId;
}

export default defineComponent({
	name: "Grid",
	components: { Plane },
	props: {
		canvasId: String,
		mapId: String,
	},
	setup: (props) => {
		const { canvasId, mapId } = props as GridProps;
		const store = useStore();

		const offsetX = computed(buildMapAccessor(mapId, "offsetX", store))
		const offsetY = computed(buildMapAccessor(mapId, "offsetY", store))
		const gridWidth = computed(buildMapAccessor(mapId, "gridWidth", store))
		const gridHeight = computed(buildMapAccessor(mapId, "gridHeight", store))

		const gridCanvas = ref({} as HTMLCanvasElement);
		let fineControl = false;

		const handleShift = (e: KeyboardEvent) => {
			fineControl = e.ctrlKey
		}

		window.addEventListener("keydown", handleShift)
		window.addEventListener("keyup", handleShift)

		onBeforeUnmount(() => {
			window.removeEventListener("keydown", handleShift)
			window.removeEventListener("keyup", handleShift)
		})

		onMounted(() => {
			gridCanvas.value = document.getElementById(canvasId) as HTMLCanvasElement;

			const grid = buildGrid(gridCanvas.value, mapId, store);

			const redrawGrid = () => {
				grid.drawGrid();
			}

			redrawGrid();

			useGridWatchers({
				grid,
				gridActions: [redrawGrid],
				mapId,
				store,
				watchStepChange: true
			})
		});

		let grabbedImage: {
			mouseX: number;
			mouseY: number;
			rescale: boolean;
		} | null = null;

		const {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick
		} = useMouseControl<number, number>({
			canvasRef: gridCanvas,
			mapId,
			store,
			transformY: a => a,
			transformX: a => a,
			onHover: (_0, _1, x, y) => {
				if (grabbedImage) {
					const mouseMovementScale = fineControl ? 0.05 : 1

					const deltaMouseX = x - grabbedImage.mouseX
					const deltaMouseY = y - grabbedImage.mouseY

					const deltaImageX = Math.round(deltaMouseX * mouseMovementScale)
					const deltaImageY = Math.round(deltaMouseY * mouseMovementScale)

					if (!grabbedImage.rescale) {
						if (Math.abs(deltaImageX) >= 1) {
							offsetX.value += deltaImageX
							grabbedImage.mouseX += deltaMouseX
						}

						if (Math.abs(deltaImageY) >= 1) {
							offsetY.value += deltaImageY
							grabbedImage.mouseY += deltaMouseY
						}
					} else {
						if (Math.abs(deltaImageX) >= 1) {
							gridWidth.value += deltaImageX
							grabbedImage.mouseX += deltaMouseX
						}

						if (Math.abs(deltaImageY) >= 1) {
							gridHeight.value -= deltaImageY
							grabbedImage.mouseY += deltaMouseY
						}
					}
				
				}
			},
			onMouseDown: (_0, _1, x, y) => {
				grabbedImage = {
					mouseX: x,
					mouseY: y,
					rescale: false
				}
			},
			onMouseUp: () => { grabbedImage = null },
			onRightClick: (_0, _1, x, y) => {
				grabbedImage = {
					mouseX: x,
					mouseY: y,
					rescale: true
				}
			}
		})

		const handleMouseLeave = () => { grabbedImage = null }
		
		return {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick,
			handleMouseLeave
		}
	},
})
