import { Ref } from "vue"
import { Store } from "vuex"
import { MapId } from "@/types"
import { buildMapAccessor } from "@/store/map";

const _noop = () => {}; // eslint-disable-line @typescript-eslint/no-empty-function

type MouseEventHandler<X, Y> = (xValue: X, yValue: Y, x: number, y: number) => void

type MouseControlConfig<X, Y> = {
	canvasRef: Ref<HTMLCanvasElement>;
	store: Store<{}>;
	mapId: MapId;
	transformX: (x: number) => X;
	transformY: (y: number) => Y;
	onHover?: MouseEventHandler<X, Y>;
	onMouseDown?: MouseEventHandler<X, Y>;
	onMouseUp?: MouseEventHandler<X, Y>;
	onRightClick?: MouseEventHandler<X, Y>;
}

type IUseMapPointFinder = <T>(p: {
	points: Ref<T[]>;
	getX: (point: T) => number;
	getY: (point: T) => number;
	pixelRadius: number;
	store: Store<{}>;
	mapId: MapId;
}) => (x: number, y: number) => number
export const useMapPointFinder: IUseMapPointFinder = ({ points, getX, getY, pixelRadius, store, mapId }) => {
	const gridWidth = buildMapAccessor(mapId, "gridWidth", store);
	const gridHeight = buildMapAccessor(mapId, "gridHeight", store);
	const minX = buildMapAccessor(mapId, "minX", store);
	const maxX = buildMapAccessor(mapId, "maxX", store);
	const minY = buildMapAccessor(mapId, "minY", store);
	const maxY = buildMapAccessor(mapId, "maxY", store);

	const deltaX = () => (maxX.get() - minX.get()) * pixelRadius / gridWidth.get()
	const deltaY = () => (maxY.get() - minY.get()) * pixelRadius / gridHeight.get()

	return (x: number, y: number): number => {
		return points.value.findIndex((point) => {
			if (getX(point) === x || Math.abs(getX(point) - x) <= deltaX()) {
				if (getY(point) === y || Math.abs(getY(point) - y) <= deltaY()) {
					return true;
				}
			}

			return false;
		})
	}
}

export const useMouseControl = <X, Y>({
	canvasRef,
	store,
	mapId,
	transformX,
	transformY,
	onHover = _noop,
	onMouseDown = _noop,
	onMouseUp = _noop,
	onRightClick = _noop,
}: MouseControlConfig<X, Y>) => {
	const gridWidth = buildMapAccessor(mapId, "gridWidth", store);
	const gridHeight = buildMapAccessor(mapId, "gridHeight", store);
	const offsetX = buildMapAccessor(mapId, "offsetX", store);
	const offsetY = buildMapAccessor(mapId, "offsetY", store);

	const minX = buildMapAccessor(mapId, "minX", store);
	const maxX = buildMapAccessor(mapId, "maxX", store);
	const minY = buildMapAccessor(mapId, "minY", store);
	const maxY = buildMapAccessor(mapId, "maxY", store);

	let canvasBoundingRect = { left: 0, top: 0 };
	const windowResizeHandler = () => { canvasBoundingRect = canvasRef.value.getBoundingClientRect() }

	const originX = () => canvasBoundingRect.left + offsetX.get()
	const originY = () => canvasBoundingRect.top + offsetY.get() - gridHeight.get()

	window.addEventListener("resize", windowResizeHandler);
	window.addEventListener("scroll", windowResizeHandler);

	const getMouseX = (x: number): number => x - canvasBoundingRect.left
	const getMouseY = (y: number): number => y - canvasBoundingRect.top

	const getMapX = (mapX: number): number => ((mapX / gridWidth.get()) * (maxX.get() - minX.get())) + minX.get()
	const getMapY = (mapY: number): number => ((1 - (mapY / gridHeight.get())) * (maxY.get() - minY.get())) + minY.get()

	const getMapDataFromAtCanvasXY = ({ x, y }: { x: number; y: number }): [number, number] => {
		const mapY = getMapY(y - originY())
		const mapX = getMapX(x - originX())

		const result: [number, number] = [
			mapX,
			mapY,
		]

		if (mapX > maxX.get()) { result[0] = maxX.get() }
		if (mapX < minX.get()) { result[0] = minX.get() }
		if (mapY > maxY.get()) { result[1] = maxY.get() }
		if (mapY < minY.get()) { result[1] = minY.get() }

		return result;
	}

	const handleMouseEvent = (e: MouseEvent): [X, Y, number, number] => {
		const [ xValue, yValue ] = getMapDataFromAtCanvasXY(e);

		return [
			transformX(xValue),
			transformY(yValue),
			getMouseX(e.x),
			getMouseY(e.y)
		];
	}

	const handleHover = (e: MouseEvent) => onHover(...handleMouseEvent(e))
	const handleMouseDown = (e: MouseEvent) => onMouseDown(...handleMouseEvent(e));
	const handleMouseUp = (e: MouseEvent) => onMouseUp(...handleMouseEvent(e));
	const handleRightClick = (e: MouseEvent) => {
		e.preventDefault();
		onRightClick(...handleMouseEvent(e));
	}

	return {
		handleHover,
		handleMouseDown,
		handleMouseUp,
		handleRightClick,
	}
}