
import { computed, defineComponent } from "vue"
import { useStore } from "vuex"
import { buildMapAccessor } from "@/store/map"
import ExternalMap from "./ExternalMap.vue"
import Grid from "./Grid.vue"
import TurbineMap from "./TurbineMap.vue"

export default defineComponent({
	name: "HotSideMap",
	components: {
		ExternalMap,
		Grid,
		TurbineMap,
	},
	setup: () => {
		const store = useStore();

		const hotSideWidth = computed(buildMapAccessor("hotSideMap", "mapWidth", store))
		const hotSideHeight = computed(buildMapAccessor("hotSideMap", "mapHeight", store))
		const hotSideLayers = computed(buildMapAccessor("hotSideMap", "layers", store))

		return {
			hotSideWidth,
			hotSideHeight,
			hotSideLayers 
		}
	}
})
