<template>
	<div
		id="cold-side-map"
		:style="{ width: coldSideWidth + 'px', height: coldSideHeight + 'px' }">
		<external-map
			v-if="coldSideLayers.map"
			canvas-id="compressor-map"
			map-id="coldSideMap" />
		<grid
			v-if="coldSideLayers.grid"
			canvas-id="compressor-grid"
			map-id="coldSideMap" />
		<turbine-map-on-compressor
			v-if="coldSideLayers.curveFromOtherside"
			canvas-id="turbine-map-on-compressor"
			map-id="coldSideMap" />
		<engine-map
			v-if="coldSideLayers.points"
			canvas-id="engine-map"
			map-id="coldSideMap" />
	</div>
</template>

<script lang="ts">
import { computed, defineComponent } from "vue"
import { useStore } from "vuex"
import { buildMapAccessor } from "@/store/map"
import ExternalMap from "./ExternalMap.vue"
import Grid from "./Grid.vue"
import TurbineMapOnCompressor from "./TurbineMapOnCompressor.vue"
import EngineMap from "./EngineMap.vue"

export default defineComponent({
	name: "ColdSideMap",
	components: {
		ExternalMap,
		Grid,
		TurbineMapOnCompressor,
		EngineMap,
	},
	setup: () => {
		const store = useStore();

		const coldSideWidth = computed(buildMapAccessor("coldSideMap", "mapWidth", store))
		const coldSideHeight = computed(buildMapAccessor("coldSideMap", "mapHeight", store))
		const coldSideLayers = computed(buildMapAccessor("coldSideMap", "layers", store))

		return {
			coldSideWidth,
			coldSideHeight,
			coldSideLayers,
		}
	}
})
</script>

<style lang="scss">
#cold-side-map {
	position: relative;
	background-color: white;
}

#compressor-map {
	position: absolute;
	top: 0;
	left: 0;
}

#compressor-grid {
	position: absolute;
	top: 0;
	left: 0;
}

#turbine-map-on-compressor {
	background-color: transparent;
	position: absolute;
	top: 0;
	left: 0;
}

#engine-map {
	background-color: transparent;
	position: absolute;
	top: 0;
	left: 0;
}
</style>
