<template>
	<plane
		:map-id="mapId"
		:layer-id="canvasId" />
</template>

<script lang="ts">
import { defineComponent, computed, onMounted, watch, onUnmounted, Ref } from "vue"
import { useStore } from "vuex";
import { buildMapAccessor } from "@/store/map";
import { getImageRedrawer } from "./graphics";
import { MapId } from "@/types";
import Plane from "./Plane.vue";

type ExternalMapProps = {
	canvasId: string;
	mapId: MapId;
}

const useFileSelector = (input: HTMLInputElement, image: HTMLImageElement, imageData: Ref<string>) => {
	const reader = new FileReader();
	if (imageData.value.length > 0) {
		image.src = imageData.value;
	}

	const readerListener = (event: ProgressEvent<FileReader>) => {
		image.src = event.target!.result as string // eslint-disable-line @typescript-eslint/no-non-null-assertion
		imageData.value = image.src
	}
	const fileSelectorListener = (event: Event) => {
		const [ imageFile ] = (event.target as unknown as { files: Array<Blob> }).files
		reader.readAsDataURL(imageFile)
	}

	reader.addEventListener("load", readerListener);
	input.addEventListener("change",fileSelectorListener);

	const cleanup = () => {
		reader.removeEventListener("load", readerListener);
		input.removeEventListener("change",fileSelectorListener);
	}

	return cleanup;
}

export default defineComponent({
	name: "ExternalMap",
	components: { Plane },
	props: {
		canvasId: String,
		gridId: String,
		mapId: String,
	},
	setup: (props) => {
		const { canvasId, mapId } = props as ExternalMapProps
		const store = useStore();

		const imageData = computed(buildMapAccessor(mapId, "imageData", store))
		const mapWidth = computed(buildMapAccessor(mapId, "mapWidth", store))
		const mapHeight = computed(buildMapAccessor(mapId, "mapHeight", store))
		const imageWidth = computed(buildMapAccessor(mapId, "imageWidth", store))
		const imageHeight = computed(buildMapAccessor(mapId, "imageHeight", store))

		let cleanupFileSelector = () => {}; // eslint-disable-line @typescript-eslint/no-empty-function

		onMounted(() => {
			const imgCanvas = document.getElementById(canvasId) as HTMLCanvasElement;
			const image = new Image();
			image.src = imageData.value
			const fileSelector = document.getElementById(`${mapId}-image`) as HTMLInputElement;

			if (fileSelector) {
				cleanupFileSelector = useFileSelector(fileSelector, image, imageData);
			}

			image.onload = () => {
				imageWidth.value = image.width
				imageHeight.value = image.height

				mapHeight.value = Math.round(mapWidth.value * (image.height / image.width))

				imgCanvas // eslint-disable-line
					.getContext("2d")!
					.drawImage(
						image,
						0,
						0,
						mapWidth.value,
						mapHeight.value,
					);
			};

			const redrawImage = getImageRedrawer({ canvas: imgCanvas, image });

			watch(
				[
					() => mapWidth.value,
					() => mapHeight.value
				],
				([w, h]) => redrawImage(w, h),
				{ flush: "post" },
			)

			watch(
				() => imageData.value,
				(imgData) => image.src = imgData,
				{ flush: "post" },
			)
		});

		onUnmounted(cleanupFileSelector)
	},
})
</script>
