
import { getState } from "@/store/local-storage";
import { buildMapAccessor, MapConfig } from "@/store/map";
import { GridId, MapId } from "@/types";
import { computed, defineComponent } from "vue";
import { useStore } from "vuex";

type GridConfigProps = {
	gridId: GridId;
	mapId: MapId;
}

export default defineComponent({
	name: "GridConfig",
	props: { gridId: String, mapId: String },
	setup: (props) => {
		const { gridId, mapId } = props as GridConfigProps
		const store = useStore();

		const mapWidth = computed(buildMapAccessor(mapId, "mapWidth", store))
		const mapHeight = computed(buildMapAccessor(mapId, "mapHeight", store))
		const minX = computed(buildMapAccessor(mapId, "minX", store))
		const maxX = computed(buildMapAccessor(mapId, "maxX", store))
		const xInterval = computed(buildMapAccessor(mapId, "xStep", store))
		const xUnits = computed(buildMapAccessor(mapId, "xUnits", store))
		const minY = computed(buildMapAccessor(mapId, "minY", store))
		const maxY = computed(buildMapAccessor(mapId, "maxY", store))
		const yInterval = computed(buildMapAccessor(mapId, "yStep", store))
		const yUnits = computed(buildMapAccessor(mapId, "yUnits", store))

		const saveState = () => store.dispatch("saveState");
		const restoreState = () => store.replaceState(getState());

		const xAxisLabel = gridId === "compressorGrid"
			? "mass airflow"
			: gridId === "turbineGrid" ? "pressure ratio"
				: "x"

		const yAxisLabel = gridId === "compressorGrid"
			? "pressure ratio"
			: gridId === "turbineGrid" ? "gasflow"
				: "x"

		const layers = computed(buildMapAccessor(mapId, "layers", store))
		const layerIndices = computed(() => {
			return Object.keys(layers.value).filter(key => !(mapId === "hotSideMap" && key === "curveFromOtherside"))
		})

		const getLayerName = (index: keyof MapConfig["layers"]): string => {
			if (index === "map") return "Map layer";
			if (index === "grid") return "Grid lines";
			if (index === "points") {
				if (gridId === "compressorGrid") return "Engine points"
				if (gridId === "turbineGrid") return "Turbine points"
			}
			if (index === "curveFromOtherside") {
				if (gridId === "compressorGrid") return "Turbine limits"
				if (gridId === "turbineGrid") return "Engine points"
			}
			return ""
		}

		return {
			mapWidth,
			mapHeight,
			minX,
			maxX,
			xInterval,
			xUnits,
			minY,
			maxY,
			yInterval,
			yUnits,
			saveState,
			restoreState,
			xAxisLabel,
			yAxisLabel,
			layers,
			layerIndices,
			getLayerName
		}
	}
})
