const GRID_STROKE_STYLE = "rgb(134,136,138)";

export const greatestCommonDivisor = (a: number, b: number): number => (b === 0
	? a
	: (greatestCommonDivisor(b, a % b)));

export class InvalidGridConfigError extends Error {
	constructor(...props: any[]) { // eslint-disable-line
		super(...props)
	}
}

type GridConstructor = {
	canvasRef: HTMLCanvasElement;
	height: number;
	width: number;
	gridWidth: number;
	gridHeight: number;
	offsetX: number;
	offsetY: number;
	minX: number;
	minY: number;
	maxX: number;
	maxY: number;
	xInterval?: number;
	yInterval?: number;
}

export class Grid {
	private context: CanvasRenderingContext2D;

	private height: number;

	private width: number;

	private gridHeight: number;

	private gridWidth: number;
	
	private offsetX: number;

	private offsetY: number;

	private minX: number;

	private minY: number;

	private maxX: number;

	private maxY: number;

	private xInterval: number;
	private yInterval: number;

	private xIntervalCount: number;
	private yIntervalCount: number;

	private xAxisSpacing: number;
	private yAxisSpacing: number;

	static validateSetup(config: GridConstructor) {
		const {
			height,
			width,
			gridHeight,
			gridWidth,
			minX,
			minY,
			maxX,
			maxY,
			yInterval = 0,
			xInterval = 0,
		} = config;

		if (height < 0 || width < 0) { throw new InvalidGridConfigError("canvas dimensions") }
		if (minX >= maxX || minY >= maxY) { throw new InvalidGridConfigError("axis limits") }
		if (yInterval < 0 || xInterval < 0) { throw new InvalidGridConfigError("axis steps") }
		if (xInterval !== 0) {
			if ((maxX - minX) / xInterval > gridWidth) { throw new InvalidGridConfigError("grid too dense") }
		}
		if (yInterval !== 0) {
			if ((maxY - minY) / yInterval > gridHeight) { throw new InvalidGridConfigError("grid too dense") }
		}
	}

	constructor(config: GridConstructor) {
		Grid.validateSetup(config);

		const {
			canvasRef,
			height,
			width,
			gridHeight,
			gridWidth,
			offsetX,
			offsetY,
			minX,
			minY,
			maxX,
			maxY,
			yInterval = 0,
			xInterval = 0,
		} = config;

		this.context = canvasRef.getContext("2d")!; // eslint-disable-line
		this.height = height;
		this.width = width;
		this.gridWidth = gridWidth;
		this.gridHeight = gridHeight;
		this.offsetX = offsetX;
		this.offsetY = offsetY;
		this.minX = minX;
		this.minY = minY;
		this.maxX = maxX;
		this.maxY = maxY;

		this.context.font = "10px sans-serif";

		this.xInterval = xInterval === 0 ? (maxX - minX) : xInterval;
		this.yInterval = yInterval === 0 ? (maxY - minY) : yInterval;

		this.xIntervalCount = Math.round((maxX - minX) / this.xInterval);
		this.yIntervalCount = Math.round((maxY - minY) / this.yInterval);

		this.xAxisSpacing = gridWidth / this.xIntervalCount;
		this.yAxisSpacing = gridHeight / this.yIntervalCount;
	}

	setup(config: Partial<GridConstructor>) {
		const mergedConfig = {
			...this,
			...config
		} as unknown as GridConstructor

		Grid.validateSetup(mergedConfig);

		this.clear()

		const {
			height,
			width,
			gridHeight,
			gridWidth,
			offsetX,
			offsetY,
			minX,
			minY,
			maxX,
			maxY,
			yInterval = 0,
			xInterval = 0,
		} = mergedConfig;

		this.height = height;
		this.width = width;
		this.gridWidth = gridWidth;
		this.gridHeight = gridHeight;
		this.offsetX = offsetX;
		this.offsetY = offsetY;
		this.minX = minX;
		this.minY = minY;
		this.maxX = maxX;
		this.maxY = maxY;

		this.context.font = "10px sans-serif";

		this.xInterval = xInterval === 0 ? (maxX - minX) : xInterval;
		this.yInterval = yInterval === 0 ? (maxY - minY) : yInterval;

		this.xIntervalCount = Math.round((maxX - minX) / this.xInterval);
		this.yIntervalCount = Math.round((maxY - minY) / this.yInterval);

		this.xAxisSpacing = gridWidth / this.xIntervalCount;
		this.yAxisSpacing = gridHeight / this.yIntervalCount;
	}

	clear() {
		this.context.clearRect(0, 0, this.width, this.height);
	}

	drawGrid() {
		this.context.beginPath();
		this.context.strokeStyle = GRID_STROKE_STYLE;
		this.context.lineWidth = 1;
		this.context.moveTo(this.offsetX, this.offsetY);

		for (let i = 0; i <= this.xIntervalCount; i += 1) {
			const tempI = Math.round(i * this.xAxisSpacing) + this.offsetX// + 0.5
			this.context.moveTo(tempI, this.offsetY);
			this.context.lineTo(tempI, this.offsetY - this.gridHeight);
		}
		for (let j = this.yIntervalCount; j >= 0; j -= 1) {
			const tempJ = Math.round(j * this.yAxisSpacing) + (this.offsetY - this.gridHeight)// + 0.5
			this.context.moveTo(this.offsetX, tempJ);
			this.context.lineTo(this.offsetX + this.gridWidth, tempJ);
		}

		this.context.stroke();
	}

	drawAxis() {
		const {
			context,
			gridWidth,
			gridHeight,
			offsetX,
			offsetY,
			xAxisSpacing,
			yAxisSpacing,
			xInterval,
			yInterval,
			minX,
			maxY,
			minY,
		} = this;

		context.beginPath();

		for (
			let x = offsetX, value = minX;
			x <= (offsetX + gridWidth);
			x += xAxisSpacing, value += xInterval
		) {
			const label = value.toFixed(3);
			context.fillText(label, x - context.measureText(label).width / 2, offsetY + 14);
		}

		for (
			let y = offsetY, value = minY;
			y >= offsetY - gridHeight;
			y -= yAxisSpacing, value += yInterval
		) {
			const label = value.toFixed(2);
			context.fillText(label, offsetX - context.measureText(label).width - 4, y + 5);
		}

		context.beginPath();
		context.rect(
			this.transformX(minX),
			this.transformY(maxY),
			this.gridWidth,
			this.gridHeight,
		);
		context.stroke();
	}

	transformX(input: number) {
		const scale = this.gridWidth / (this.maxX - this.minX);

		return Math.floor((input - this.minX) * scale) + this.offsetX + 0.5;
	}

	transformY(input: number) {
		const scale = this.gridHeight / (this.maxY - this.minY);

		return this.offsetY - Math.floor((input - this.minY) * scale) +0.5;
	}

	moveTo(x: number, y: number) {
		return this.context.moveTo(
			this.transformX(x),
			this.transformY(y),
		);
	}

	lineTo(x: number, y: number) {
		return this.context.lineTo(
			this.transformX(x),
			this.transformY(y),
		);
	}

	stroke() {
		return this.context.stroke();
	}

	drawCircle(x: number, y: number, radius: number, fill = false) {
		this.context.beginPath()
		this.context.moveTo(this.transformX(x) + radius, this.transformY(y))
		this.context.arc(this.transformX(x), this.transformY(y), radius, 0, Math.PI * 2)
		if (fill) {
			this.context.fill()
		}
		this.context.stroke()
	}

	drawMap(points: { x: number; y: number }[]) {
		this.clear();
		if (points.length === 0) { return; }

		this.context.beginPath();

		this.context.strokeStyle = "green";
		this.context.lineWidth = 3;

		const [first, ...restPoints] = points;
		this.moveTo(first.x, first.y);

		restPoints.forEach(({ x, y }) => {
			this.lineTo(x, y);
			this.moveTo(x, y);
		});

		this.lineTo(first.x, first.y);
		this.context.closePath();
		this.stroke();

		this.context.beginPath();
		this.context.strokeStyle = "red";
		this.context.fillStyle = "pink";
		this.context.lineWidth = 1;

		points.forEach(({ x, y }) => {
			this.drawCircle(x, y, 5, true)
		})
	}

	drawCurve(points: { x: number; y: number }[]) {
		this.clear();
		if (points.length === 0) { return; }

		this.context.beginPath();

		this.context.strokeStyle = "green";
		this.context.lineWidth = 3;

		const [first, ...restPoints] = points;
		this.moveTo(first.x, first.y);

		restPoints.forEach(({ x, y }) => {
			this.lineTo(x, y);
			this.moveTo(x, y);
		});
		this.stroke();
		this.context.closePath();

		this.context.beginPath();
		this.context.strokeStyle = "red";
		this.context.fillStyle = "pink";
		this.context.lineWidth = 1;

		points.forEach(({ x, y }) => {
			this.drawCircle(x, y, 5, true)
		})
	}

	plotFunction(f: (x: number) => number, minX = this.minX, maxX = this.maxX) {
		this.clear();
		this.context.strokeStyle = "red";
		const step = (maxX - minX) / this.gridWidth;
		this.context.beginPath();
		this.moveTo(this.minX, f(this.minX));
		for (let x = minX; x < maxX; x += step) {
			this.lineTo(x, f(x))
		}
		this.context.stroke();
	}
}

export const getImageRedrawer = (
	{ image, canvas }: { image: CanvasImageSource; canvas: HTMLCanvasElement },
) => (
	width: number,
	height: number,
) => {
	const {
		width: canvasWidth,
		height: canvasHeight,
	} = canvas;

	const ctx = canvas.getContext("2d")!; // eslint-disable-line

	ctx.clearRect(0, 0, canvasWidth, canvasHeight);
	ctx.drawImage(
		image,
		0,
		0,
		width,
		height,
	);
};
