
import { defineComponent, computed, onMounted, watch, ref } from "vue"
import { useStore } from "vuex";
import {
	buildMapPointsGetter,
	buildAccessor as buildEngineMapAccessor,
	buildMapPointModifier,
	buildMapPointInserter,
	buildMapPointRemover,
} from "@/store/engine-map";
import { useGridWatchers, buildGrid } from "./grid-watchers";
import { MassAirflow, MassAirflowUnits, Power, Pressure } from "@/physics/units";
import { getCompressorPower, getMAF, getRPMfromMAF } from "@/physics/calculations";
import { useMapPointFinder, useMouseControl } from "./mouse-control";
import Tooltip from "@/components/Tooltip.vue";
import { MapId } from "@/types";
import Plane from "./Plane.vue";
import { buildMapAccessor } from "@/store/map";

type EngineMapProps = {
	canvasId: string;
	mapId: MapId;
}

export default defineComponent({
	name: "Map",
	components: { Plane, Tooltip },
	props: {
		canvasId: String,
		mapId: String,
	},
	setup: (props) => {
		const { canvasId, mapId } = props as EngineMapProps;

		const tooltipX = ref(0)
		const tooltipY = ref(0)
		const tooltipVisible = ref(false)

		const setTooltipPosition = (x: number, y: number) => {
			tooltipX.value = x;
			tooltipY.value = y;
		}
		const hideTooltip = () => tooltipVisible.value = false;
		const showTooltip = () => {
			tooltipVisible.value = true
			window.dispatchEvent(new Event('scroll'))
		};

		const graphCanvas = ref({} as HTMLCanvasElement);

		const store = useStore();

		const minPressureRatio = computed(buildMapAccessor(mapId, "minY", store));
		const xAxisUnits = computed(buildMapAccessor(mapId, "xUnits", store));
		const airflowUnits = computed(() => {
			if (xAxisUnits.value === "corrected lbs/min") {
				return MassAirflowUnits.lbsmin
			}
			return MassAirflowUnits.kgs
		});

		const engineDisplacement = computed(buildEngineMapAccessor("displacement", store));
		const ambientAirPressure = computed(buildEngineMapAccessor("ambientAirPressure", store));
		const ambientAirTemperature = computed(buildEngineMapAccessor("ambientAirTemperature", store));
		const pointerData = ref({
			boost: 0,
			rpm: 0,
			compressorPower: new Power(0),
			maf: new MassAirflow(0)
		});

		const setMapPoint = buildMapPointModifier(store);
		const insertMapPoint = buildMapPointInserter(store);
		const removeMapPoint = buildMapPointRemover(store);

		// const correctMaf = (maf: number): number => {
		// 	return maf
		// 		* Math.sqrt(ambientAirTemperature.value.SI / Temperature.standardDay())
		// 		* (Pressure.standardDay() / ambientAirPressure.value.SI)
		// }

		// const uncorrectMaf = (maf: number): number => {
		// 	return maf
		// 		* Math.sqrt((ZERO_CELSIUS_KELVIN + 25) / ambientAirTemperature.value.SI)
		// 		* (ambientAirPressure.value.SI / Pressure.standardDay())
		// }

		const getMAFlocal = ({ pressure, rpm }: { pressure: Pressure; rpm: number }): number => {
			const actualMaf = getMAF({
				displacement: engineDisplacement.value,
				temperature: ambientAirTemperature.value,
				pressure,
				rpm,
			})

			return actualMaf[airflowUnits.value];
		}

		const engineMapRawPoints = computed(buildMapPointsGetter(store));
		const engineMapPoints = computed(() => {
			return [
				{
					x: getMAFlocal({
						pressure: ambientAirPressure.value,
						rpm: engineMapRawPoints.value[0]?.rpm
					}),
					y: minPressureRatio.value
				},
				...engineMapRawPoints.value.map(p => ({
					x: getMAFlocal({
						pressure: p.pressure,
						rpm: p.rpm
					}),
					y: p.pressure.getPressureRatio(ambientAirPressure.value)
				})),
				{
					x: getMAFlocal({
						pressure: ambientAirPressure.value,
						rpm: engineMapRawPoints.value[engineMapRawPoints.value.length - 1]?.rpm
					}),
					y: minPressureRatio.value
				}
			]
		})

		onMounted(() => {
			graphCanvas.value = document.getElementById(canvasId) as HTMLCanvasElement;

			const graph = buildGrid(graphCanvas.value, mapId, store)

			graph.drawMap(engineMapPoints.value)

			useGridWatchers({
				grid: graph,
				gridActions: [() => { graph.drawMap(engineMapPoints.value) }],
				store,
				mapId,
			})

			watch(
				() => engineMapPoints.value,
				(points) => { graph.drawMap(points) },
				{ flush: "post" }
			)
		});

		let contextPoint = -1;

		const getRPM = ({ massAirflow, pressureRatio }: { massAirflow: MassAirflow; pressureRatio: number }): number => Math.round(getRPMfromMAF({
			massAirflow,
			displacement: engineDisplacement.value,
			temperature: ambientAirTemperature.value,
			pressure: ambientAirPressure.value.multiply(pressureRatio)
		}))

		const findMapPointIndex = useMapPointFinder({
			points: engineMapPoints,
			getX: point => point.x,
			getY: point => point.y,
			pixelRadius: 10,
			store,
			mapId,
		})

		const {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick
		} = useMouseControl<MassAirflow, number>({
			canvasRef: graphCanvas,
			mapId,
			store,
			transformY: a => a,
			transformX: (maf: number) => new MassAirflow(maf, airflowUnits.value),
			onHover: (massAirflow: MassAirflow, pressureRatio: number, x, y) => {
				setTooltipPosition(x, y)
				const rpm = getRPM({ massAirflow, pressureRatio })

				pointerData.value = {
					rpm,
					boost: ambientAirPressure.value.multiply((pressureRatio - 1))["psi"],
					compressorPower: getCompressorPower({
						massAirflow,
						ambientTemperature: ambientAirTemperature.value,
						pressureRatio,
						compressorEfficiency: 0.72
					}),
					maf: massAirflow,
				}

				if (contextPoint > -1) {
					setMapPoint(
						{
							rpm,
							pressure: ambientAirPressure.value.multiply(pressureRatio)
						},
						contextPoint
					)
				}
			},
			onMouseDown: (massAirflow: MassAirflow, pressureRatio: number) => {
				contextPoint = findMapPointIndex(massAirflow[airflowUnits.value], pressureRatio) - 1;
			},
			onMouseUp: () => { contextPoint = -1 },
			onRightClick: (massAirflow: MassAirflow, pressureRatio: number) => {
				const pointIndex = findMapPointIndex(massAirflow[airflowUnits.value], pressureRatio) - 1;

				if (pointIndex < 0) {
					insertMapPoint({
						rpm: getRPM({ massAirflow, pressureRatio }),
						pressure: ambientAirPressure.value.multiply(pressureRatio)
					});
				} else {
					removeMapPoint(engineMapRawPoints.value[pointIndex]);
				}
			}
		})

		return {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick,
			pointerData,
			airflowUnits,
			tooltipX,
			tooltipY,
			tooltipVisible,
			showTooltip,
			hideTooltip,
		}
	},
})
