/* eslint-disable @typescript-eslint/no-non-null-assertion */

import { Unit, CLASS_KEY } from "./Unit";

export enum VolumeUnit {
	cc = "cubic centimeters",
	m3 = "cubic meters",
	SI = "cubic meters"
}

export const VOLUME_CLASS_ID = Symbol.for("volumeUnit").toString();

export class Volume implements Unit {
	private _cc = 0;
	private _m3 = 0;

	constructor(value: number, unit = VolumeUnit.m3) {
		this[unit] = value;
	}

	toJSON() {
		return {
			[CLASS_KEY]: VOLUME_CLASS_ID,
			[VolumeUnit.m3]: this.SI
		}
	}

	get SI () { return this._m3; }

	set [VolumeUnit.cc] (value: number) {
		this._cc = value;
		this._m3 = value / 1000000;
	}
	get [VolumeUnit.cc] () { return this._cc }

	set [VolumeUnit.m3] (value: number) {
		this._m3 = value;
		this._cc = value * 1000000
	}
	get [VolumeUnit.m3] () { return this._m3 }
}
