import { Module, Store } from "vuex"

export const NAMESPACE = "turbineModel";

type TurbineMapPoint = {
	gasflow: number;
	pressureRatio: number;
}

export type TurbineModelState = {
	points: TurbineMapPoint[];
}

const namespaced = (input: "points" | "addPoint" | "removePoint" | "changePoint"): string => `${NAMESPACE}/${input}`;

export const buildMapPointsGetter = (store: Store<{}>) => (): TurbineMapPoint[] => store.getters[namespaced("points")]
export const buildMapPointInserter = (store: Store<{}>) => (point: TurbineMapPoint) => store.commit(namespaced("addPoint"), point)
export const buildMapPointRemover = (store: Store<{}>) => (point: TurbineMapPoint) => store.commit(namespaced("removePoint"), point)
export const buildMapPointModifier = (store: Store<{}>) => (point: TurbineMapPoint, index: number) => store.commit(namespaced("changePoint"), { point, index })

export const turbineModel: Module<TurbineModelState, {}> = {
	namespaced: true,
	state: {
		points: [
			{ gasflow: 0,		pressureRatio: 1 },
			{ gasflow: 9.2,		pressureRatio: 1.175 },
			{ gasflow: 12.1,		pressureRatio: 1.375 },
			{ gasflow: 13,	pressureRatio: 1.5 },
			{ gasflow: 14.4,	pressureRatio: 1.75 },
			{ gasflow: 16,		pressureRatio: 2.25 },
			{ gasflow: 16.25,		pressureRatio: 2.5 },
			{ gasflow: 16.4,	pressureRatio: 3 },
			{ gasflow: 16.2,	pressureRatio: 3.75 }
		]
	},
	getters: {
		points: ({ points }) => points
	},
	mutations: {
		addPoint: (state, point: TurbineMapPoint) => {
			state.points.push(point);
			state.points.sort((a, b) => a.pressureRatio - b.pressureRatio);
		},
		removePoint: (state, point: TurbineMapPoint) => {
			const index = state.points.findIndex(p => p.pressureRatio === point.pressureRatio && p.gasflow === point.gasflow)
			state.points.splice(index, 1);
		},
		changePoint: (state, { index, point }: { index: number; point: TurbineMapPoint }) => {
			state.points[index] = point;
		}
	}
}