
import { defineComponent, computed, onMounted, watch } from "vue"
import { useStore } from "vuex";
import { buildAccessor as buildEngineMapAccessor } from "@/store/engine-map";
import { useGridWatchers, buildGrid } from "./grid-watchers";
import { MassAirflow, MassAirflowUnits, Pressure, Temperature } from "@/physics/units";
import { buildInterpolator, getTurbinePower, uncorrectGasflow, uncorrectPhi } from "@/physics/calculations";
import { buildMapPointsGetter as buildTurbineMapPointsGetter } from "@/store/turbine-model";
import { CP_AIR_288K, GAMMA_AIR_288K } from "@/physics/constants";
import { MapId } from "@/types";
import Plane from "./Plane.vue";
import { buildMapAccessor } from "@/store/map";

type EngineMapProps = {
	canvasId: string;
	mapId: MapId;
}

export default defineComponent({
	name: "TurbineMapOnCompressor",
	components: { Plane },
	props: {
		canvasId: String,
		mapId: String,
	},
	setup: (props) => {
		const { canvasId, mapId } = props as EngineMapProps;

		let graphCanvas: HTMLCanvasElement;
		const store = useStore();

		const ambientAirTemperature = computed(buildEngineMapAccessor("ambientAirTemperature", store));

		const xCompressorAxisUnits = computed(buildMapAccessor("coldSideMap", "xUnits", store));
		const compressorAirflowUnits = computed(() => {
			if (xCompressorAxisUnits.value === "corrected lbs/min") {
				return MassAirflowUnits.lbsmin
			}
			return MassAirflowUnits.kgs
		});

		const yTurbineAxisUnits = computed(buildMapAccessor("hotSideMap", "yUnits", store));
		
		const uncorrectLocalGasflow = (maf: number, pressureRatio: number): MassAirflow => {
			if (yTurbineAxisUnits.value === "phi") {
				return uncorrectPhi({
					phi: maf,
					pressureRatio,
					referencePressure: new Pressure(101325),
					exhaustTemperature: new Temperature(1100)
				})
			}
			return uncorrectGasflow({
				correctedFlow: new MassAirflow(maf, MassAirflowUnits.lbsmin),
				pressureRatio,
				referenceTemperature: new Temperature(288.15),
				exhaustTemperature: new Temperature(1100)
			})
		}

		const turbineMapPoints = computed(buildTurbineMapPointsGetter(store));

		const interpolateTurbinePower = buildInterpolator(
			() => turbineMapPoints.value,
			({ gasflow, pressureRatio }) => uncorrectLocalGasflow(gasflow, pressureRatio)[compressorAirflowUnits.value],
			({ gasflow, pressureRatio }) => getTurbinePower({
				massAirflow: uncorrectLocalGasflow(gasflow, pressureRatio),
				pressureRatio,
				inletTemperature: new Temperature(1100),
				turbineEfficiency: 0.69
			}).SI,
		)

		const impossiblePressures = (maf: number): number => {
			const afr = 1 + (1 / 11.3);
			const a = interpolateTurbinePower(maf * afr) * 0.59
			const b = new MassAirflow(maf, compressorAirflowUnits.value).SI * CP_AIR_288K * ambientAirTemperature.value.SI;
			const c = (a / b) + 1;

			return Math.pow(c, GAMMA_AIR_288K / (GAMMA_AIR_288K - 1));
		}

		onMounted(() => {
			graphCanvas = document.getElementById(canvasId) as HTMLCanvasElement;

			const graph = buildGrid(graphCanvas, mapId, store);

			graph.plotFunction(impossiblePressures)

			useGridWatchers({
				grid: graph,
				gridActions: [() => graph.plotFunction(impossiblePressures)],
				mapId,
				store
			})

			watch(
				() => turbineMapPoints.value.reduce((agg, p) => agg + p.pressureRatio + p.gasflow, 0),
				() => { graph.plotFunction(impossiblePressures) },
				{ flush: "post" }
			)
		});
	},
})
