import {
	CLASS_KEY,
	MassAirflow,
	MassAirflowUnits,
	MASS_AIRFLOW_CLASS_ID,
	Power,
	PowerUnit,
	POWER_CLASS_ID,
	Pressure,
	PressureUnit,
	PRESSURE_CLASS_ID,
	Temperature,
	TemperatureUnit,
	TEMPERATURE_CLASS_ID,
	Volume,
	VolumeUnit,
	VOLUME_CLASS_ID
} from "@/physics/units";

export const reviveUnits = (_: string, obj: any): any => { // eslint-disable-line @typescript-eslint/no-explicit-any
	const {
		[CLASS_KEY]: classId
	} = obj

	switch(classId) {
	case MASS_AIRFLOW_CLASS_ID: {
		return new MassAirflow(obj[MassAirflowUnits.SI])
	}
	case POWER_CLASS_ID: {
		return new Power(obj[PowerUnit.SI])
	}
	case PRESSURE_CLASS_ID: {
		return new Pressure(obj[PressureUnit.SI])
	}
	case TEMPERATURE_CLASS_ID: {
		return new Temperature(obj[TemperatureUnit.SI])
	}
	case VOLUME_CLASS_ID: {
		return new Volume(obj[VolumeUnit.SI])
	}
	}

	return obj
}
