import { Ref, watch } from "vue"
import { Grid, InvalidGridConfigError } from "./graphics"
import { Store } from "vuex"
import { buildMapAccessor } from "@/store/map"
import { MapId } from "@/types";

type GridParamGetters = [
	() => Ref<number>["value"], // plane height
	() => Ref<number>["value"], // plane width
	() => Ref<number>["value"], // grid height
	() => Ref<number>["value"], // grid width
	() => Ref<number>["value"], // offsetX
	() => Ref<number>["value"], // offsetY
	() => Ref<number>["value"], // minX
	() => Ref<number>["value"], // maxX
	() => Ref<number>["value"], // minY
	() => Ref<number>["value"], // maxY
	() => Ref<string>["value"], // Xunits
	() => Ref<string>["value"], // Yunits
	() => Ref<number>["value"], // xStep
	() => Ref<number>["value"], // yStep
];

type GridWatchersConfig = {
	grid: Grid;
	mapId: MapId;
	gridActions: (() => void)[];
	watchStepChange?: boolean;
	store: Store<{}>;
}

export const buildGrid = (
	canvasRef: HTMLCanvasElement,
	mapId: MapId,
	store: Store<{}>
) => new Grid({
	canvasRef,
	width: buildMapAccessor(mapId, "mapWidth", store).get(),
	height: buildMapAccessor(mapId, "mapHeight", store).get(),
	gridWidth: buildMapAccessor(mapId, "gridWidth", store).get(),
	gridHeight: buildMapAccessor(mapId, "gridHeight", store).get(),
	offsetX: buildMapAccessor(mapId, "offsetX", store).get(),
	offsetY: buildMapAccessor(mapId, "offsetY", store).get(),
	minX: buildMapAccessor(mapId, "minX", store).get(),
	maxX: buildMapAccessor(mapId, "maxX", store).get(),
	minY: buildMapAccessor(mapId, "minY", store).get(),
	maxY: buildMapAccessor(mapId, "maxY", store).get(),
	xInterval: buildMapAccessor(mapId, "xStep", store).get(),
	yInterval: buildMapAccessor(mapId, "yStep", store).get(),
});

export const useGridWatchers = (config: GridWatchersConfig) => {
	const {
		grid,
		mapId,
		gridActions,
		watchStepChange = false,
		store,
	} = config;

	const accessors = [
		buildMapAccessor(mapId, "mapWidth", store),
		buildMapAccessor(mapId, "mapHeight", store),
		buildMapAccessor(mapId, "gridWidth", store),
		buildMapAccessor(mapId, "gridHeight", store),
		buildMapAccessor(mapId, "offsetX", store),
		buildMapAccessor(mapId, "offsetY", store),
		buildMapAccessor(mapId, "minX", store),
		buildMapAccessor(mapId, "maxX", store),
		buildMapAccessor(mapId, "minY", store),
		buildMapAccessor(mapId, "maxY", store),
		buildMapAccessor(mapId, "xUnits", store), 
		buildMapAccessor(mapId, "yUnits", store), 
	]

	if (watchStepChange) {
		accessors.push(buildMapAccessor(mapId, "xStep", store))
		accessors.push(buildMapAccessor(mapId, "yStep", store))
	}

	const accessorGetters = accessors.map(accessor => accessor.get) as GridParamGetters;

	watch<GridParamGetters>(
		accessorGetters,
		(newValues) => {
			const [
				width,
				height,
				gridWidth,
				gridHeight,
				offsetX,
				offsetY,
				minX,
				maxX,
				minY,
				maxY,
				xUnits, // eslint-disable-line @typescript-eslint/no-unused-vars
				yUnits, // eslint-disable-line @typescript-eslint/no-unused-vars
				xInterval,
				yInterval
			] = newValues

			try {
				grid.setup({
					width,
					height,
					gridWidth,
					gridHeight,
					offsetX,
					offsetY,
					minX,
					maxX,
					xInterval,
					minY,
					maxY,
					yInterval
				})

				gridActions.forEach(a => a())
			} catch (err) {
				if (!(err instanceof InvalidGridConfigError)) {
					console.log(err) // eslint-disable-line
				}
			}
		},
		{ flush: "post" }
	)
}