<template>
	<div>
		<form class="grid-config-form">
			<fieldset class="grid-config-fieldset">
				<label :for="`${mapId}-image`">Select image</label>
				<input :id="`${mapId}-image`" type="file" />
			</fieldset>

			<fieldset class="grid-config-fieldset">
				<label :for="`${gridId}-width`">Width</label>
				<input :id="`${gridId}-width`" type="number" v-model.number="mapWidth" :step="20" />
				<label :for="`${gridId}-height`">Height</label>
				<input :id="`${gridId}-height`" type="number" v-model.number="mapHeight" :step="20" />
			</fieldset>

			<fieldset class="grid-config-fieldset">
				<label :for="`${gridId}-minX`">Minimum {{ xAxisLabel }}</label>
				<div class="field-with-units">
					<input :id="`${gridId}-minX`" type="number" v-model.number="minX" :step="xInterval" />
					<select :id="`${gridId}-xUnits`" v-model="xUnits">
						<option value="pressureRatio">pressure ratio</option>
						<optgroup label="Corrected mass airflow">
							<option value="corrected lbs/min">lbs/min</option>
							<option value="corrected kg/s">kg/s</option>
						</optgroup>
					</select>
				</div>
				<label :for="`${gridId}-maxX`">Maximum {{ xAxisLabel }}</label>
				<div class="field-with-units">
					<input :id="`${gridId}-minX`" type="number" v-model.number="maxX" :step="xInterval" />
					<select :id="`${gridId}-xUnits`" v-model="xUnits">
						<option value="pressureRatio">pressure ratio</option>
						<optgroup label="Corrected mass airflow">
							<option value="corrected lbs/min">lbs/min</option>
							<option value="corrected kg/s">kg/s</option>
						</optgroup>
					</select>
				</div>
				<label :for="`${gridId}-xInterval`">Grid step</label>
				<input :id="`${gridId}-xInterval`" type="number" v-model.number="xInterval" step="any" />
				<label :for="`${gridId}-minY`">Minimum {{ yAxisLabel }}</label>
				<div class="field-with-units">
					<input :id="`${gridId}-minY`" type="number" v-model.number="minY" :step="yInterval" />
					<select :id="`${gridId}-yUnits`" v-model="yUnits">
						<option value="pressureRatio">pressure ratio</option>
						<optgroup label="Mass airflow">
							<option value="corrected lbs/min">lbs/min (Garret)</option>
							<option value="phi">phi (Borg-Warner)</option>
						</optgroup>
					</select>
				</div>
				<label :for="`${gridId}-maxY`">Maximum {{ yAxisLabel }}</label>
				<div class="field-with-units">
					<input :id="`${gridId}-maxY`" type="number" v-model.number="maxY" :step="yInterval" min="1" />
					<select :id="`${gridId}-yUnits`" v-model="yUnits">
						<option value="pressureRatio">pressure ratio</option>
						<optgroup label="Mass airflow">
							<option value="corrected lbs/min">lbs/min (Garret)</option>
							<option value="phi">phi (Borg-Warner)</option>
						</optgroup>
					</select>
				</div>
				<label :for="`${gridId}-yInterval`">Grid step</label>
				<input :id="`${gridId}-yInterval`" type="number" v-model.number="yInterval" step="any" />
			</fieldset>
			<fieldset class="grid-config-fieldset">
				<template v-for="(index) in layerIndices" :key="index">
					<label :for="`${gridId}-layers-${index}`">{{ getLayerName(index) }}</label>
					<input :id="`${gridId}-layers-${index}`" type="checkbox" v-model="layers[index]" />
				</template>
			</fieldset>
		</form>

		<button @click="saveState">Save state</button>
		<button @click="restoreState">Restore saved state</button>
	</div>
</template>

<script lang="ts">
import { getState } from "@/store/local-storage";
import { buildMapAccessor, MapConfig } from "@/store/map";
import { GridId, MapId } from "@/types";
import { computed, defineComponent } from "vue";
import { useStore } from "vuex";

type GridConfigProps = {
	gridId: GridId;
	mapId: MapId;
}

export default defineComponent({
	name: "GridConfig",
	props: { gridId: String, mapId: String },
	setup: (props) => {
		const { gridId, mapId } = props as GridConfigProps
		const store = useStore();

		const mapWidth = computed(buildMapAccessor(mapId, "mapWidth", store))
		const mapHeight = computed(buildMapAccessor(mapId, "mapHeight", store))
		const minX = computed(buildMapAccessor(mapId, "minX", store))
		const maxX = computed(buildMapAccessor(mapId, "maxX", store))
		const xInterval = computed(buildMapAccessor(mapId, "xStep", store))
		const xUnits = computed(buildMapAccessor(mapId, "xUnits", store))
		const minY = computed(buildMapAccessor(mapId, "minY", store))
		const maxY = computed(buildMapAccessor(mapId, "maxY", store))
		const yInterval = computed(buildMapAccessor(mapId, "yStep", store))
		const yUnits = computed(buildMapAccessor(mapId, "yUnits", store))

		const saveState = () => store.dispatch("saveState");
		const restoreState = () => store.replaceState(getState());

		const xAxisLabel = gridId === "compressorGrid"
			? "mass airflow"
			: gridId === "turbineGrid" ? "pressure ratio"
				: "x"

		const yAxisLabel = gridId === "compressorGrid"
			? "pressure ratio"
			: gridId === "turbineGrid" ? "gasflow"
				: "x"

		const layers = computed(buildMapAccessor(mapId, "layers", store))
		const layerIndices = computed(() => {
			return Object.keys(layers.value).filter(key => !(mapId === "hotSideMap" && key === "curveFromOtherside"))
		})

		const getLayerName = (index: keyof MapConfig["layers"]): string => {
			if (index === "map") return "Map layer";
			if (index === "grid") return "Grid lines";
			if (index === "points") {
				if (gridId === "compressorGrid") return "Engine points"
				if (gridId === "turbineGrid") return "Turbine points"
			}
			if (index === "curveFromOtherside") {
				if (gridId === "compressorGrid") return "Turbine limits"
				if (gridId === "turbineGrid") return "Engine points"
			}
			return ""
		}

		return {
			mapWidth,
			mapHeight,
			minX,
			maxX,
			xInterval,
			xUnits,
			minY,
			maxY,
			yInterval,
			yUnits,
			saveState,
			restoreState,
			xAxisLabel,
			yAxisLabel,
			layers,
			layerIndices,
			getLayerName
		}
	}
})
</script>

<style lang="scss" scoped>
.grid-config-form {
	display: flex;
	flex-direction: column;
}

.grid-config-fieldset {
	display: grid;
	grid-template-columns: 50% 50%;
}

.field-with-units {
	display: grid;
	grid-template-columns: minmax(0, 1fr) 130px;
}

</style>
