<template>
	<div
		id="hot-side-map"
		:style="{ width: hotSideWidth + 'px', height: hotSideHeight + 'px' }">
		<external-map
			v-if="hotSideLayers.map"
			canvas-id="turbine-map"
			map-id="hotSideMap" />
		<grid
			v-if="hotSideLayers.grid"
			canvas-id="turbine-grid"
			map-id="hotSideMap" />
		<turbine-map
			v-if="hotSideLayers.points"
			canvas-id="turbine-model"
			map-id="hotSideMap" />
	</div>
</template>

<script lang="ts">
import { computed, defineComponent } from "vue"
import { useStore } from "vuex"
import { buildMapAccessor } from "@/store/map"
import ExternalMap from "./ExternalMap.vue"
import Grid from "./Grid.vue"
import TurbineMap from "./TurbineMap.vue"

export default defineComponent({
	name: "HotSideMap",
	components: {
		ExternalMap,
		Grid,
		TurbineMap,
	},
	setup: () => {
		const store = useStore();

		const hotSideWidth = computed(buildMapAccessor("hotSideMap", "mapWidth", store))
		const hotSideHeight = computed(buildMapAccessor("hotSideMap", "mapHeight", store))
		const hotSideLayers = computed(buildMapAccessor("hotSideMap", "layers", store))

		return {
			hotSideWidth,
			hotSideHeight,
			hotSideLayers 
		}
	}
})
</script>

<style lang="scss">
#hot-side-map {
	position: relative;
	background-color: white;
	width: 1175px;
}

#turbine-map {
	position: absolute;
	top: 0;
	left: 0;
}

#turbine-grid {
	position: absolute;
	top: 0;
	left: 0;
}

#turbine-model {
	background-color: transparent;
	position: absolute;
	top: 0;
	left: 0;
}
</style>
