<template>
	<canvas
		:id="layerId"
		:width="width"
		:height="height" />
</template>

<script lang="ts">
import { computed, defineComponent } from "vue";
import { useStore } from "vuex";
import { MapId } from "@/types";
import { buildMapAccessor } from "@/store/map";

type PlaneProps = {
	mapId: MapId;
	layerId: string;
}

export default defineComponent({
	name: "Plane",
	props: {
		mapId: String,
		layerId: String,
	},
	setup: (props) => {
		const { mapId } = props as PlaneProps;
		const store = useStore();
		const width = computed(buildMapAccessor(mapId, "mapWidth", store))
		const height = computed(buildMapAccessor(mapId, "mapHeight", store))

		return {
			width,
			height,
		}
	}
})
</script>

