
import { defineComponent, computed, onMounted, watch, ref } from "vue"
import { useStore } from "vuex";
import {
	buildMapPointInserter,
	buildMapPointRemover,
	buildMapPointModifier,
	buildMapPointsGetter
} from "@/store/turbine-model";
import { buildGrid, useGridWatchers } from "./grid-watchers";
import { MassAirflowUnits, MassAirflow, Power, Temperature, Pressure } from "@/physics/units";
import { useMapPointFinder, useMouseControl } from "./mouse-control";
import { getTurbinePower, uncorrectGasflow, uncorrectPhi } from "@/physics/calculations";
import Tooltip from "@/components/Tooltip.vue"
import Plane from "./Plane.vue"
import { MapId } from "@/types";
import { buildMapAccessor } from "@/store/map";

type TurbineMapProps = {
	canvasId: string;
	mapId: MapId;
}

export default defineComponent({
	name: "TurbineMap",
	components: { Plane, Tooltip },
	props: {
		canvasId: String,
		mapId: String,
	},
	setup: (props) => {
		const { canvasId, mapId } = props as TurbineMapProps;

		const tooltipX = ref(0)
		const tooltipY = ref(0)
		const tooltipVisible = ref(false)

		const setTooltipPosition = (x: number, y: number) => {
			tooltipX.value = x;
			tooltipY.value = y;
		}
		const hideTooltip = () => tooltipVisible.value = false;
		const showTooltip = () => tooltipVisible.value = true;

		const graphCanvas = ref({} as HTMLCanvasElement);

		const store = useStore();

		const yUnits = computed(buildMapAccessor(mapId, "yUnits", store));
		const airflowUnits = computed(() => {
			if (yUnits.value === "corrected lbs/min") {
				return MassAirflowUnits.lbsmin
			}

			if (yUnits.value === "phi") {
				return MassAirflowUnits.kgs
			}
		})

		const uncorrectLocalGasflow = (maf: number, pressureRatio: number): MassAirflow => {
			if (yUnits.value === "phi") {
				return uncorrectPhi({
					phi: maf,
					pressureRatio,
					referencePressure: new Pressure(101325),
					exhaustTemperature: new Temperature(1100)
				})
			}
			return uncorrectGasflow({
				correctedFlow: new MassAirflow(maf, MassAirflowUnits.lbsmin),
				pressureRatio,
				referenceTemperature: new Temperature(288.15),
				exhaustTemperature: new Temperature(1100)
			})
		}

		const setMapPoint = buildMapPointModifier(store);
		const insertMapPoint = buildMapPointInserter(store);
		const removeMapPoint = buildMapPointRemover(store);
		const turbineMapPointsRaw = buildMapPointsGetter(store);
		const turbineMapPoints = computed(
			() => turbineMapPointsRaw().map(p => ({
				y: p.gasflow,
				x: p.pressureRatio
			}))
		)

		const pointerData = ref({
			turbinePower: new Power(0),
			phi: 0,
		});

		onMounted(() => {
			graphCanvas.value = document.getElementById(canvasId) as HTMLCanvasElement;

			const graph = buildGrid(graphCanvas.value, mapId, store);

			graph.drawCurve(turbineMapPoints.value)

			useGridWatchers({
				grid: graph,
				gridActions: [() => { graph.drawCurve(turbineMapPoints.value) }],
				store,
				mapId
			})

			watch(
				() => turbineMapPoints.value,
				points => { graph.drawCurve(points) },
				{ flush: "post" }
			)
		});

		let contextPoint = -1;

		const findMapPointIndex = useMapPointFinder({
			points: computed(turbineMapPointsRaw),
			getX: (point) => point.pressureRatio,
			getY: (point) => point.gasflow,
			pixelRadius: 10,
			store,
			mapId,
		})

		const {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick
		} = useMouseControl<number, number>({
			canvasRef: graphCanvas,
			mapId,
			store,
			transformY: a => a,
			transformX: a => a,
			onHover: (pressureRatio, airflow, x, y) => {
				setTooltipPosition(x, y)
				pointerData.value = {
					turbinePower: getTurbinePower({
						massAirflow: uncorrectLocalGasflow(airflow, pressureRatio),
						inletTemperature: new Temperature(1100),
						pressureRatio,
						turbineEfficiency: 0.70
					}),
					phi: airflow
				}

				if (contextPoint > -1) {
					setMapPoint(
						{
							pressureRatio,
							gasflow: airflow,
						},
						contextPoint
					)
				}
			},
			onMouseDown: (pressureRatio, airflow) => {
				contextPoint = findMapPointIndex(pressureRatio, airflow);
			},
			onMouseUp: () => { contextPoint = -1 },
			onRightClick: (pressureRatio, airflow) => {
				const pointIndex = findMapPointIndex(pressureRatio, airflow);

				if (pointIndex < 0) {
					insertMapPoint({
						pressureRatio,
						gasflow: airflow,
					});
				} else {
					removeMapPoint(turbineMapPointsRaw()[pointIndex]);
				}
			},
		})

		return {
			handleHover,
			handleMouseDown,
			handleMouseUp,
			handleRightClick,
			airflowUnits,
			pointerData,
			tooltipX,
			tooltipY,
			tooltipVisible,
			showTooltip,
			hideTooltip,
		}
	},
})
