/* eslint-disable @typescript-eslint/camelcase */
import {
	AIR_SPECIFIC_GAS_CONSTANT,
	GAMMA_AIR_288K,
	GAMMA_AIR_1100K,
	CP_AIR_1100K,
	CP_AIR_288K
} from "@/physics/constants"
import {
	MassAirflow,
	Power,
	Pressure,
	Temperature,
	Volume,
} from "@/physics/units"

type KgM3 = number

type IGetAirDensity = (p: { temperature: Temperature; pressure: Pressure }) => KgM3
export const getAirDensity: IGetAirDensity = ({ temperature, pressure }) => (
	pressure.SI / (temperature.SI * AIR_SPECIFIC_GAS_CONSTANT)
);

type IGetVolume = (p: {
	displacement: Volume;
	volumetricEfficiency?: number;
	throttling?: number;
}) => Volume
export const getVolume: IGetVolume = ({
	displacement,
	volumetricEfficiency = 1,
	throttling = 1,
}) => {
	displacement["cubic meters"] = displacement.SI * volumetricEfficiency * throttling
	return displacement;
};

type IGetVolumeFlow = <T extends number>(p: { rpm: number; volume: T }) => number
export const getVolumeFlow: IGetVolumeFlow = ({ rpm, volume }) => (
	(volume * rpm) / (2 * 60)
);

type IGetRPMfromMAF = (p: {
	massAirflow: MassAirflow;
	displacement: Volume;
	temperature: Temperature;
	pressure: Pressure;
}) => number
export const getRPMfromMAF: IGetRPMfromMAF = ({
	massAirflow, displacement, temperature, pressure
}) => {
	const airDensity = getAirDensity({ temperature, pressure })
	const volumeFlow = massAirflow.SI / airDensity;
	const rpm = volumeFlow * 60 * 2 / displacement.SI;
	return rpm
}

type IGetMAF = (p: {
	rpm: number;
	displacement: Volume;
	temperature: Temperature;
	pressure: Pressure;
}) => MassAirflow
export const getMAF: IGetMAF = ({
	rpm,
	displacement,
	temperature,
	pressure,
}) => {
	const volume = getVolume({ displacement });
	return new MassAirflow(getVolumeFlow({ rpm, volume: volume.SI }) * getAirDensity({ temperature, pressure }))
};

type IGetEMAF = (p: {
	intakeAirflow: MassAirflow;
	stoichiometricAirFuelRatio: number;
	lambda: number;
}) => MassAirflow;
export const getEMAF: IGetEMAF = ({
	intakeAirflow,
	stoichiometricAirFuelRatio,
	lambda
}) => {
	const massScale = 1 + 1 / (stoichiometricAirFuelRatio * lambda) // lambda = afr/afrstoich

	return new MassAirflow(intakeAirflow.SI * massScale);
}

const GAMMA_RATIO_S_288K = (GAMMA_AIR_288K - 1) / GAMMA_AIR_288K
const GAMMA_RATIO_S_1100K = (GAMMA_AIR_1100K - 1) / GAMMA_AIR_1100K

type IGetCompressorPower = (p: {
	massAirflow: MassAirflow;
	ambientTemperature: Temperature;
	pressureRatio: number;
	compressorEfficiency: number;
}) => Power
export const getCompressorPower: IGetCompressorPower = ({
	massAirflow,
	ambientTemperature,
	pressureRatio,
	compressorEfficiency,
}) => {
	const p = (1 / compressorEfficiency)
		* massAirflow.SI
		* CP_AIR_288K
		* ambientTemperature.SI
		* (Math.pow(pressureRatio, GAMMA_RATIO_S_288K) - 1);

	return new Power(p);
}

type IGetTurbinePower = (p: {
	massAirflow: MassAirflow;
	inletTemperature: Temperature;
	pressureRatio: number;
	turbineEfficiency: number;
}) => Power
export const getTurbinePower: IGetTurbinePower = ({
	massAirflow,
	inletTemperature,
	pressureRatio,
	turbineEfficiency
}) => {
	const p = turbineEfficiency
		* massAirflow.SI
		* CP_AIR_1100K
		* inletTemperature.SI
		*	(1 - Math.pow(1/pressureRatio, GAMMA_RATIO_S_1100K));

	return new Power(p);
}

export const buildInterpolator = <T>(
	getDataPoints: () => T[],
	getX: (i: T) => number,
	getY: (i: T) => number,
) => (x: number): number => {
		const dataPoints = getDataPoints();
		if (dataPoints.length === 0) {
			return 0;
		}

		const exactMatch = dataPoints.find(p => getX(p) === x);
		if (exactMatch) { return getY(exactMatch) }

		const bigIndex = dataPoints.findIndex(p => getX(p) > x);
		const smallIndex = bigIndex - 1;

		if (bigIndex === -1) {
			return getY(dataPoints[dataPoints.length - 1])
		}

		if (bigIndex === 0) {
			return getY(dataPoints[0]);
		}

		const deltaX = getX(dataPoints[bigIndex]) - getX(dataPoints[smallIndex]);
		const deltaY = getY(dataPoints[bigIndex]) - getY(dataPoints[smallIndex]);

		return getY(dataPoints[bigIndex]) - (deltaY * (getX(dataPoints[bigIndex]) - x) / deltaX);
	}

type IUncorrectPhi = (p: {
	phi: number;
	pressureRatio: number;
	referencePressure: Pressure;
	exhaustTemperature: Temperature;
}) => MassAirflow
export const uncorrectPhi: IUncorrectPhi = ({
	phi,
	pressureRatio,
	referencePressure,
	exhaustTemperature
}) => new MassAirflow(phi * pressureRatio * referencePressure["kilopascal"] / Math.sqrt(exhaustTemperature.SI))

type IUncorrectGasflow = (p: {
	correctedFlow: MassAirflow;
	pressureRatio: number;
	referenceTemperature: Temperature;
	exhaustTemperature: Temperature;
}) => MassAirflow
export const uncorrectGasflow: IUncorrectGasflow = ({
	correctedFlow,
	pressureRatio,
	referenceTemperature,
	exhaustTemperature
}) => new MassAirflow(correctedFlow.SI * pressureRatio * Math.sqrt(referenceTemperature.SI/exhaustTemperature.SI))
