<template>
	<div
		class="tooltip"
		:class="{ 'tooltip--visible': visible }"
		:style="{ left: x + 'px', top: y + 'px' }"
		:id="tooltipId"
		@mouseover="handleMouseenter"
		@mouseout="handleMouseleave">
		<slot />
	</div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from "vue";

export default defineComponent({
	props: {
		x: Number,
		y: Number,
		visible: Boolean,
		tooltipId: String,
	},
	setup: (props) => {
		const self = ref<HTMLElement>({} as HTMLElement);
		

		onMounted(() => {
			self.value = document.getElementById(props.tooltipId!) as HTMLElement; // eslint-disable-line @typescript-eslint/no-non-null-assertion
		})

		return {
			handleMouseenter: () => { self.value.className = "tooltip" },
			handleMouseleave: () => { self.value.className = "tooltip tooltip--visible" },
		}
	}
})
</script>

<style lang="scss" scoped>
.tooltip {
	display: none;
	flex-direction: column;
	align-items: flex-start;
	background: #C8C8C7;
	margin: 30px;
	padding: 10px;
	position: absolute;
	z-index: 1000;
	width: 250px;

	&--visible {
		display: grid;
		grid-template-columns: 1fr 1fr;
		justify-items: start;
		column-gap: 10px;
	}
}
</style>