import { createStore } from "vuex";
import { MapConfigState, map, NAMESPACE as mapNamespace } from "./map";
import { EngineMapState, engineMap, NAMESPACE as engineMapNamespace } from "./engine-map"
import { TurbineModelState, turbineModel, NAMESPACE as turbineModelNamespace } from "./turbine-model";
import { setState } from "./local-storage";

export type State = {
	[mapNamespace]: MapConfigState;
	[engineMapNamespace]: EngineMapState;
	[turbineModelNamespace]: TurbineModelState;
}

export default createStore<State>({
	modules: {
		[mapNamespace]: map,
		[engineMapNamespace]: engineMap,
		[turbineModelNamespace]: turbineModel,
	},
	mutations: {
		state: (state, payload) => { state = payload; }
	},
	actions: {
		saveState: ({ state }) => setState(state)
	}
});
