import { ABSOLUTE_ZERO_CELSIUS } from "@/physics/constants";
import { Unit, CLASS_KEY } from "./Unit";

export enum TemperatureUnit {
	K = "kelvin",
	C = "celsius",
	F = "farenheit",
	SI = "kelvin",
}

const celsiusToFarenheit = (celsius: number): number => celsius * 9 / 5 + 32;
const celsiusToKelvin = (celsius: number): number => celsius - ABSOLUTE_ZERO_CELSIUS;
const farenheitToCelsius = (farenheit: number): number => (farenheit - 32) * 5 / 9;
const farenheitToKelvin = (farenheit: number): number => ((farenheit - 32) * 5 / 9) - ABSOLUTE_ZERO_CELSIUS
const kelvinToCelsius = (kelvin: number): number => kelvin + ABSOLUTE_ZERO_CELSIUS;
const kelvinToFarenheit = (kelvin: number): number => (kelvin + ABSOLUTE_ZERO_CELSIUS) * 9 / 5 + 32

export const TEMPERATURE_CLASS_ID = Symbol.for("temperatureUnit").toString();

export class Temperature implements Unit {
	private _K = 0;
	private _C = -1 * ABSOLUTE_ZERO_CELSIUS;
	private _F = kelvinToFarenheit(0);
	static standardDay(units = TemperatureUnit.K) {
		return new Temperature(288.15)[units]
	}

	constructor(value: number, unit = TemperatureUnit.K) {
		this[unit] = value;
	}

	toJSON() {
		return {
			[CLASS_KEY]: TEMPERATURE_CLASS_ID,
			[TemperatureUnit.K]: this.SI
		}
	}

	get SI () { return this._K }

	set [TemperatureUnit.K] (value: number) {
		this._K = value;
		this._C = kelvinToCelsius(value);
		this._F = kelvinToFarenheit(value);
	}
	get [TemperatureUnit.K] () { return this._K; }

	set [TemperatureUnit.C] (value: number) {
		this._K = celsiusToKelvin(value);
		this._C = value;
		this._F = celsiusToFarenheit(value);
	}
	get [TemperatureUnit.C] () { return this._C; }

	set [TemperatureUnit.F] (value: number) {
		this._K = farenheitToKelvin(value);
		this._C = farenheitToCelsius(value);
		this._F = value;
	}
	get [TemperatureUnit.F] () { return this._F; }
}
